import React from 'react'
import CircleButton from './CircleButton.js'
function Header(props) {
    return (
        <div className="header">
            <CircleButton menuClass=" sideMenu" icon={"menu"}>
                <div onClick={props.newNoteProp} className="menuItem"> + New + </div>
                {props.titles.map(title=>
                    (<div className="menuItem" key={title.key} onClick={()=>props.loadNote(title.key)}>
                        <CircleButton onClick={()=>props.editNote(title.key)} noMenu={true} icon={"edit"}></CircleButton>
                        {title.title}
                    </div>)
                )}                                
            </CircleButton>
            <CircleButton  icon={"person"} menuClass="rightMenu">

            </CircleButton>
        </div>
    )
}
Header.defaultProps={
    titles:[{title:"default title", key:"default key"}],
    loadNote:()=>{},
    editNote:()=>{console.log("edit note not function")},
    newNoteProp:()=>{console.log("abc")}
}
export default Header
